import React, { Component } from "react";
import { Link } from "react-router-dom";
import {
  Card,
  CardHeader,
  CardBody,
  Col,
  Row,
  Table,
  Dropdown,
  DropdownItem,
  DropdownMenu,
  DropdownToggle
} from "reactstrap";
import { getRiskAreas } from "../RiskAreas/api";
//import { getHash } from "../../_url";

class RiskRegisters extends Component {
  state = {
    riskAreas: [],
    dropdownOpen: false,
    riskStatus: ""
  };

  componentDidMount() {
    getRiskAreas((err, riskAreas) => {
      if (err) return;
      this.setState({
        riskAreas: riskAreas
      });
    });
  }
  toggle = () => {
    const dropdownOpen = this.state.dropdownOpen;
    this.setState({ dropdownOpen: !dropdownOpen });
  };
  onItemSelect = status => {
    this.setState({ riskStatus: status ? status : "" });
  };
  render() {
    const { riskAreas, riskStatus } = this.state;
    const unique = [...new Set(riskAreas.map(riskArea => riskArea.isActive))];
    const filtered = riskStatus
      ? riskAreas.filter(riskArea => riskArea.isActive === riskStatus.isActive)
      : riskAreas;
    // console.log(filtered)
    // const permissions = this.props.permissions ? this.props.permissions : {};
    return (
      <div className="animated fadeIn">
        <Row>
          <Col xs="12" lg="12">
            <Card>
              <CardHeader>
                <Row>
                  <Col>
                    <Row>
                      <i className="fa fa-cog fa-2x ml-2 mt-2"></i>{" "}
                      <h4 className="m-2">Risk Registers</h4>
                    </Row>
                  </Col>
                  <Col>
                    <Dropdown
                      isOpen={this.state.dropdownOpen}
                      toggle={this.toggle}
                    >
                      <DropdownToggle caret color="" style={{ float: "right" }}>
                        Active/InActive
                      </DropdownToggle>
                      <DropdownMenu>
                        {unique.map((riskArea, indx) => {
                          return (
                            <DropdownItem
                              onClick={() => this.onItemSelect(riskArea)}
                              key={riskArea}
                            >
                              {riskArea ? "Active" : "InActive"}
                            </DropdownItem>
                          );
                        })}
                      </DropdownMenu>
                    </Dropdown>
                  </Col>
                </Row>
              </CardHeader>
              <CardBody className="table-responsive">
                <Table>
                  <thead>
                    <tr>
                      <th>S.N.</th>
                      <th>Name</th>
                      <th>Created</th>
                      <th>Updated</th>
                    </tr>
                  </thead>
                  <tbody>
                    {filtered.map((riskArea, index) => (
                      <tr key={index}>
                        <td>{index + 1}</td>
                        <td>
                          <Link to={"/risk-register/" + riskArea.code}>
                            {riskArea.name}
                          </Link>
                        </td>
                        <td>{riskArea.createdAt}</td>
                        <td>{riskArea.updatedAt}</td>
                      </tr>
                    ))}
                  </tbody>
                </Table>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    );
  }
}

export default RiskRegisters;
